import React, { useEffect, useState } from 'react';
import axios from 'axios';
import { Link } from 'react-router-dom';
import 'bootstrap/dist/css/bootstrap.min.css';
import PropTypes from 'prop-types'
import { makeStyles } from '@material-ui/core/styles';
import AppBar from '@material-ui/core/AppBar';
import Toolbar from '@material-ui/core/Toolbar';
import Typography from '@material-ui/core/Typography';
import Button from '@material-ui/core/Button';
import IconButton from '@material-ui/core/IconButton';
import MenuIcon from '@material-ui/icons/Menu';

const useStyles = makeStyles((theme) => ({
    root: {
      flexGrow: 1,
    },
    menuButton: {
      marginRight: theme.spacing(2),
    },
    title: {
      flexGrow: 1,
    },
  }));

export default function Navbar({ name }) {
    const classes = useStyles();

    const [types, setTypes] = useState([])

    useEffect(() => {
        axios({
            method: "GET",
            url: 'http://127.0.0.1:8000/api/type/'
        }).then(response => {
            setTypes(response.data)
        })
    }, [])

    const logout = async () => {
        await fetch('http://127.0.0.1:8000/api/logout', {
            method: 'POST',
            headers: { 'Content-Type': 'application/json' },
            credentials: 'include',
        });
    }

    let menu;

    if (name === undefined) {
        menu = (
            <Toolbar>
                <IconButton edge="start" className={classes.menuButton} color="inherit" aria-label="menu">
                <MenuIcon />
                </IconButton>
                <Typography variant="h6" className={classes.title}>
                    <Link to='/'>WorkMaster</Link>
                </Typography>
                <Button color="inherit"><Link to='/login'>Войти</Link></Button>
                <Button color="inherit"><Link to='/register'>Зарегистрироваться</Link></Button>
            </Toolbar>
        )
    } else {
        menu = (
            <Toolbar>
                <IconButton edge="start" className={classes.menuButton} color="inherit" aria-label="menu">
                <MenuIcon />
                </IconButton>
                <Typography variant="h6" className={classes.title}>
                    <Link to='/' className="navbar-brand">WorkMaster</Link>
                </Typography>
                {types.map(c => (
                <Typography variant="h6" className={classes.title}>
                    <Link to={{ pathname: `/type/${c.id}/`, fromDashboard: false }} key={c.id}>{c.name}</Link>
                </Typography>
                    ))}
                <Typography variant="h6" className={classes.title}>
                    <Link to='/myposts'>Мои посты</Link>
                </Typography>
                <Typography variant="h6" className={classes.title}>
                    <Link to='/newpost'>Создать пост</Link>
                </Typography>
                <Button color="inherit"><Link to='/login' onClick={logout}>Выйти</Link></Button>
            </Toolbar>
        )
    }

    return (
        <AppBar position="static">
                        {menu}
        </AppBar>
    );
}
Navbar.propTypes = {
    name: PropTypes.string
}